
.header-contenedor{
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
    background: #e2e2e2 ;
    opacity: 0.9;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    backdrop-filter: blur(8px);
}

.contenedor-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid var(--black) 2px;
    width: fit-content;
    margin-top: 1rem;
}

.logo{
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 600;
    margin: 0.5rem 0.5rem; 
    color: var(--black);
    text-decoration: none;
}



.redes{
    display: flex;
    gap: 2rem;
    color: var(--black);
}

.red{
    font-size: 2rem;
}

.red a{
    color: var(--black);
}

.red a:hover{
    color: var(--red);
    transition: 0.2s;
}

@media(max-width: 768px) {
    .header-contenedor{
        display: flex;
        flex-direction: column;
        width: 100%;
        gap: 1rem;
    }

    .redes{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        background-color: var(--red);
        height: 100%;
        width: 8rem;
        padding: 30px;
        opacity: .9;
        transform: translateX(-500px);
        transition: ease-in-out;
    }

    .red{
        margin-top: 2rem ;
    }

    .redes.show{
        transform: translateX(0);
        
    }

    .contenedor-logo{
        margin-top: 0.5rem;
    }

    .logo{
        font-weight: bold;
    }

}