.contenedor-principal{
    width: 100%;
    margin: 8rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

@media(max-width: 768px) {
    .contenedor-principal{
        width: 100%;
        margin: 9rem auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
    }
}

.contacto-links{

    display: grid;
    gap: 2rem;
    text-transform: uppercase;
}

.contacto-links a{
    text-decoration: none;
    color: white;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 0.5rem;
    width: 200px;
    height: 50px;
    background-color:var(--black);
}

.contacto-links a:hover{
    background-color: rgb(78, 78, 78);
    transition: 0.2s;
}

.contacto-links a:last-child{
    background-color: var(--red);
}
.contacto-links a:last-child:hover{
    background-color: rgb(207, 8, 8);
}


/*CONTACTO*/

.contenedor-contacto{
    font-weight: 500;
}

.contenedor-contacto span{
    color: var(--red);
}

.mailme{
    text-align: center;
    margin-top: 2rem;
  
}
.mailme a{
    text-decoration: none;
    color: var(--black);
}
.mailme h4{
    border: solid 2px var(--black);
    padding: 0.5rem 0.5rem;
    letter-spacing: 2px;
}